@import url(//spoqa.github.io/spoqa-han-sans/css/SpoqaHanSansNeo.css);
*{margin:0;padding:0;-webkit-box-sizing: border-box;box-sizing: border-box;font-family: 'Spoqa Han Sans Neo', 'sans-serif';
}
#app {
}

#container {
  text-align: center;
  color: #fff;
  background: rgb(37, 46, 148);
  background: radial-gradient(circle,
      rgba(37, 46, 148, 1) 0%,
      rgba(0, 0, 0, 1) 100%);
  min-height: 90vh;
  text-align: center;
  padding: 25vh 0;
  position: relative;
}
h1 {
  font-size: 40px;
  font-weight: 600;
}
form {
  bottom: 0;
  position: absolute;
  width: 100%;
  border-radius: 40px 40px 0 0;
  background-color: #fff;
  -webkit-box-shadow: 0 -20px 20px rgba(0, 0, 0, 0.144);
          box-shadow: 0 -20px 20px rgba(0, 0, 0, 0.144);
  color: #000;
  padding: 50px 0;
}
h2 {
  font-weight: 200;
  font-size: 30px;
}
h3 {
  font-weight: 200;
  font-size: 20px;
}
span {
  font-weight: 400;
}
input {
  border-left: none;
  border-right: none;
  border-top: none;
  display: inline-block;
  line-height: 1.6;
  font-size: 20px;
  padding-top: 20px;
  text-align: center;
  outline: none;
}
img {
  width: 100px;
}
